import styled, { css } from 'styled-components';
import theme from '../theme';

const commonStyles = `
  padding: 11px 28px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  border: 2px solid;
  margin-right: 10px;
  margin-top: 10px;
  font-family: Arial;
  font-weight: 600;
`;

export const StyledButtonComponent = styled.button`
  ${commonStyles}
  height: 38px;
  padding-top: 10px;
  display: inline-block;
  background-color: ${({ type }) => theme?.[type]};
  color: white;
  text-align: center;
  text-decoration: none;
`;

export const OutlineStyledButtonComponent = styled.button`
  ${commonStyles}
  height: 40px;
  background-color: ${theme.badgeText};
  color: ${({ type }) => theme?.[type]};
  border: 1px solid ${({ type }) => theme?.[type]};

  &:hover,
  &:active {
    background-color: ${({ type }) => theme?.[type]};
    color: ${theme.badgeText};
  }
`;

export const TextStyledButtonComponent = styled.button`
  ${commonStyles}
  color: ${({ type }) => theme?.[type]};
  background-color: transparent;
  border: none;
`;

const getButtonStyles = (variant) => {
  switch (variant) {
    case 'text':
      return css`
        ${TextStyledButtonComponent}
      `;
    case 'outlined':
      return css`
        ${OutlineStyledButtonComponent}
      `;
    default:
      return css``;
  }
};

export const ConditionalStyledButtonComponent = styled.button`
  ${commonStyles}
  ${({ variant }) => getButtonStyles(variant)}
`;

export const IconStyledButtonComponent = styled.button`
  ${commonStyles}
  height: 40px;
  padding-top: 8px;
  display: inline-block;
  background-color: ${({ type }) => theme?.[type]};
  color: white;
  text-align: center;
  text-decoration: none;
`;

export const IconsStyledButtonComponent = styled.button`
  ${commonStyles}
  height: 38px;
  padding-top: 8px;
  background-color: ${theme.badgeText};
  color: ${({ type }) => theme?.[type]};
  border: 1px solid ${({ type }) => theme?.[type]};

  &:hover,
  &:active {
    background-color: ${({ type }) => theme?.[type]};
    color: ${theme.badgeText};
  }
`;

export const Icon = styled.span`
  font-size: 16px;
  margin-top: 12px;
  margin-right: 10px;
`;
